// font-family, font-size, line-height    .page

.process {
  padding-top: 21px; //             ,              .container
  padding-bottom: 49px;

  @media (min-width: $tablet-width) {
    padding-top: 45px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 72px;
  }
}

.process__list {
  margin: 0;
  padding: 0; //     ol
  list-style-type: none;
  color: $special_dark-grey;

  @media (min-width: $tablet-width) {
    padding-top: 32px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 60px; //       
    row-gap: 36px; //        

    counter-reset: procces; //     process (          )
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: repeat(4, 1fr);
    //     
    column-gap: 80px;
    //    80px
  }
}

.process__item {
  display: grid;
  grid-template-columns: 60px 1fr;
  gap: 20px;


  @media (min-width: $tablet-width) {
    gap: 32px;
    position: relative;
    //        ,     process__item
    z-index: 0;
    //  ,         
    min-height: 280px;
    //  (  )        
    padding-top: 40px;
    //        
    padding-right: 72px;
    //                  

    grid-template-columns: auto; //      ,      
    grid-template-rows: 80px auto; //          (      80 px)

    counter-increment: process; //                counter-increment                           . -> counter-increment: example 2;
  }

  @media (min-width: $desktop-width) {
    padding-right: 0;
  }

  & + & {
    //          ,  not:(first-child) 
    @media (max-width: $mobile-width-only) {//    :                           
      margin-top: 22px;
    }
  }

  &::before {
    //       .        (  )     ,    ::before           ,        ( ),        ()       .
    content: "";

    @include size (60px);
    //    60    mixins.scss, 60 px       
    margin-top: -2px;
    //      (       2 px)
    background-color: $color-primary;
    //     
    background-repeat: no-repeat;
    //         
    background-position: 50% 50%;

    @media (min-width: $tablet-width) {
      @include size (80px); //        80px
      margin-top: 0; //    (-2)       - 
    }
  }

  &::after {
    @media (min-width: $tablet-width) {
      //         
      content: counter(process);
      //               
      position: absolute;
      //        process__item  position: relative      
      top: 0;
      //      process__item
      right: 80px;
      //             80 -   (109-30)    (105-30)
      z-index: -1;
      //     .     
      color: $basic_grey;
      font-family: $font-heading;
      font-size: 280px;
      font-weight: 400;
      line-height: 1;/* 100% */
    }

    @media (min-width: $desktop-width) {
      right: 0;
    }
  }

  //       16px
  &:first-of-type::after {
    @media (min-width: $tablet-width) {
      margin-right: 16px;
    }
  }
}

//      ,  
.process__item--leaf::before {
  background-image: url("../img/icons/icon_leaf.svg");
}

.process__item--powder::before {
  background-image: url("../img/icons/icon_tub.svg");
}

.process__item--food::before {
  background-image: url("../img/icons/icon_eat.svg");
}

.process__item--scales::before {
  background-image: url("../img/icons/icon_alarm.svg");
}

.proccess__br {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
  }
}
